.banner {
    color: white;
    object-fit: contain;
    height: 448px;
    /* position: relative;  */
    /* Add this line to position the gradient overlay */
}

.banner__contents{
    margin-left: 30px;
    padding-top: 140px;
    height: 190px;
}

.banner__title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}

.banner__description{
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 0.8rem;
    max-width: 360px;
    height: 80px;
}

  
  .banner__button {
    cursor: pointer;
    color: white;
    border: none;
    outline: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    margin-right: 1rem;
    background-color: rgb(51, 51, 51,0.5);
  
    
  }

  .banner__button:hover {
    transform: scale(1.1);
  }
  
  .banner__button:hover {
    color: #000;
    background-color: #e6e6e6;
    transition: all 0.2s;
  }
  
  .banner__button--play {
    background-color: #e50914;
    margin-right: 1rem; /* Add margin between buttons */
  }
  
  .banner__button--my-list {
    background-color: rgba(0, 0, 0, 0.6);
    color: #e50914;
  }
  
  .banner--fadeBottom {
    /* Update the height to match your design */
    height: 27.4rem;
    background-image: linear-gradient(
        180deg,
        transparent,
        rgba(37, 37, 37, 0.61),
        #111
    );
    bottom: 0;
    left: 0;
    right: 0;
}